.black-block {
  .wrapper {
    background:            url("../../img/black-bg.webp") center / cover no-repeat;
    border-radius:         16px;
    overflow:              hidden;
    position:              relative;
    display:               grid;
    grid-template-columns: 1fr 1fr;
  }

  .content {
    max-width: 670px;
    padding:   40px;
  }

  .title {
    margin-bottom: 20px;

    span {
      color: var(--bezhevyy);
    }
  }

  .text-d {
    color:         var(--belyy);
    margin-bottom: 80px;
  }

  &__btn {
    display:     flex;
    align-items: center;
    gap:         10px;
  }

  .right {
    display: flex;
    gap:     20px;

    .image {
      display: flex;
      justify-content: end;
      align-items: end;
      img {
        display: block;

      }
    }

    .right-text {
      max-width:     340px;
      margin-top:    auto;
      margin-bottom: 40px;
      font-family:   var(--font-family);
      font-weight:   400;
      font-size:     14px;
      line-height:   130%;
      color:         var(--belyy);

      a {
        text-decoration:          underline;
        text-decoration-skip-ink: none;
        color:                    inherit;
        transition:               color .3s ease-in-out;
        @include hover {
          &:hover {
            color: var(--bezhevyy);
          }
        }
      }
    }
  }
}