.complex {
  .title {
    color: var(--chernyy);
    max-width: 780px;
    margin: 0 0 20px 0;
  }

  .text-d {
    margin: 0 0 60px 0;
    max-width: 710px;
  }

  .inner {
    display: grid;
    grid-template-columns: 1fr 568px;
    gap: clamp(20px, 2.5vw, 40px);
  }

  .items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;

    .item {
      border-radius: 8px;
      padding: 20px;
      background: var(--goluboy);
      position: relative;
      &:before {
        content: '';
        position: absolute;
        width: 48px;
        height: 48px;
        background: url('../../img/icons/check-3d.png') no-repeat;
        background-size: cover;
      }
      p {
        margin: 0;
        font-family: var(--second-family);
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        color: var(--chernyy);
        padding-left: 68px;

      }
    }
  }

  .image-block {
    position: relative;
    padding: 20px;
    display: flex;
    flex-direction: column;
    img {
      position: absolute;
      inset: 0;
      z-index: -1;
      border-radius: 8px;
    }

    .subtitle {
      font-family: var(--second-family);
      font-weight: 400;
      @include r-font(30, 24);
      text-transform: uppercase;
      color: var(--belyy);
      margin: 0 0 20px 0;
    }

    .button {
      margin-top: auto;
      max-width: 340px;
    }
  }
}