.callback {
  .wrapper {
    background: url("../../img/black-bg.webp") center center no-repeat;
    background-size: cover;
    border-radius: 16px;
    overflow: hidden;
    padding: 10px 40px;
    position: relative;
    @media(min-width: 767px) {
      padding: 40px;
    }
  }
  .image {
    position: absolute;
    bottom: 0;
    right: 0;
    img {
      display: block;
    }
  }
  .desc {
    margin: 0;
    margin-bottom: 68px;
    color: var(--belyy);
    max-width: 685px;
  }
  .title {
    margin-bottom: 14px;
    span {
      color: var(--bezhevyy);
    }
  }

  .calling {
    border-radius: 8px;
    padding: 14px 24px;
    background: rgba(243, 245, 255, 0.7);
    max-width: 515px;
    margin-bottom: 60px;
    span {
      display: block;
      margin-bottom: 4px;
    }

    a {
      font-family: var(--second-family);
      font-weight: 700;
      @include r-font(24, 20);
      line-height: 120%;
      color: var(--chernyy);
      transition: color .3s ease-in-out;
      @include hover {
        &:hover {
          color: var(--hover-btn);
        }
      }
    }
  }

  .actions {
    span {
      display: inline-block;
      font-family: var(--second-family);
      font-weight: 700;
      @include r-font(20,18);
      line-height: 120%;
      color: var(--belyy);
      margin-bottom: 20px;
    }

    .buttons {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;

      a {
        display: grid;
        grid-template-columns: 1fr 24px;
        place-items: center;
        align-items: center;
        gap: 10px;
        font-family: var(--font-family);
        font-weight: 500;
        @include r-font(18, 16);
        color: var(--belyy);
        border: 1px solid #e9ebf6;
        border-radius: 4px;
        padding: 12px 48px;
        transition: background-color 0.3s;
        .icon {
          width: 24px;
          height: 24px;
          display: grid;
          place-items: center;
          margin: 0;
        }
        @include hover {
          &:hover {
            background: var(--hover-btn);
            border-color: transparent;
          }
        }
      }
    }
  }
}

.callback-2 {
  .wrapper {
    background: url("../../img/bg-black-2.webp") center center no-repeat;
  }
}