.memorial {
  .title {
    color:         var(--chernyy);
    margin-bottom: 20px;
  }

  .text-d {
    margin: 0;
  }

  .desc {
    max-width:     815px;
    margin-bottom: clamp(40px, 3vw, 60px);
  }


  .inner {
    display:   flex;
    flex-wrap: wrap;
    gap:       20px;
  }

  .item {
    flex:                  1 1 450px;
    padding:               14px;
    border-radius:         8px;
    background:            var(--goluboy);
    display:               grid;
    grid-template-columns: 100px 1fr;
    gap:                   20px;
    align-items:           center;

    .content {
      display:        flex;
      flex-direction: column;
      gap:            8px;
    }

    .subtitle {
      font-family: var(--second-family);
      font-weight: 700;
      @include r-font(24, 20);
      line-height: 120%;
      color:       var(--chernyy);
    }
  }

  &__link {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    gap: 10px;
    max-width: 340px;
    margin: clamp(20px, 2.5vw, 40px) auto 0 auto;
  }
}