html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;

}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;

}

.page__body {
  margin: 0;
  min-width: 370px;
  min-height: 100%;
  font-size: 16px;
  overflow-x: hidden;
  background-color: var(--belyy);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}


.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden-sm {
  @media (max-width: 768px) {
    display: none !important;
  }
}

.title {
  font-family: var(--second-family);
  font-weight: 400;
  @include r-font(40, 28);
  line-height: 120%;
  text-transform: uppercase;
  color: var(--belyy);
  margin: 0;
}

.text-d {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: var(--chernyy);
}

.icon {
  width: 40px;
  height: 40px;
  display: grid;
  place-items: center;
  background: linear-gradient(226deg, #f1b378 0%, #b97637 100%);
  border-radius: 50%;

  transition: scale .3s ease-in-out;
  @include hover {
    &:hover {
      scale: 1.05;
    }
  }
}

.btn--accent {
  border-radius: 4px;
  padding: 12px 48px;
  background: var(--bezhevyy);
  font-family: var(--font-family);
  font-weight: 500;
  @include r-font(18, 16);
  color: var(--chernyy);
  transition: background 0.2s;

  @include hover {
    &:hover {
      background: var(--hover-btn);
      color: var(--belyy);
    }
  }
}

.btn--border {
  border-radius: 4px;
  padding: 12px 48px;
  background: transparent;
  font-family: var(--font-family);
  font-weight: 500;
  @include r-font(18, 16);
  color: var(--chernyy);
  transition: background 0.2s;
  border: 1px solid var(--bezhevyy);
  border-radius: 4px;

  @include hover {
    &:hover {
      background: var(--hover-btn);
      color: var(--belyy);
    }
  }
}

.section {
  margin-bottom: 120px;
  @media(max-width: 767px) {
    margin-bottom: 80px;
  }
}
