:root {
  --font-family: "Inter", sans-serif;
  --second-family: "PT Serif", sans-serif;
  --content-width: 1440px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --chernyy: #23211f;
  --siniy: #09165d;
  --bezhevyy: #e6ab73;
  --goluboy: #f3f5ff;
  --belyy: #fff;
  --seryy: #f8f9fc;
  --hover-btn: #B16E2F;
}

/* stylelint-disable */
/* stylelint-disable */
.burger__wrapper {
  background-color: #545B2F;
  border-radius: 50px;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable value-keyword-case */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 370px;
  min-height: 100%;
  font-size: 16px;
  overflow-x: hidden;
  background-color: var(--belyy);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

@media (max-width: 768px) {
  .hidden-sm {
    display: none !important;
  }
}

.title {
  font-family: var(--second-family);
  font-weight: 400;
  line-height: 120%;
  text-transform: uppercase;
  color: var(--belyy);
  margin: 0;
}
@media (max-width: 767px) {
  .title {
    font-size: calc(28px + 3.6 * (100vw - 320px) / 1440);
  }
}
@media (min-width: 767px) {
  .title {
    font-size: calc(28px + 12 * 100vw / 1440);
  }
}

.text-d {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: var(--chernyy);
}

.icon {
  width: 40px;
  height: 40px;
  display: grid;
  place-items: center;
  background: linear-gradient(226deg, #f1b378 0%, #b97637 100%);
  border-radius: 50%;
  transition: scale 0.3s ease-in-out;
}
@media (any-hover: hover) {
  .icon:hover:hover {
    scale: 1.05;
  }
}

.btn--accent {
  border-radius: 4px;
  padding: 12px 48px;
  background: var(--bezhevyy);
  font-family: var(--font-family);
  font-weight: 500;
  color: var(--chernyy);
  transition: background 0.2s;
}
@media (max-width: 767px) {
  .btn--accent {
    font-size: calc(16px + 0.6 * (100vw - 320px) / 1440);
  }
}
@media (min-width: 767px) {
  .btn--accent {
    font-size: calc(16px + 2 * 100vw / 1440);
  }
}
@media (any-hover: hover) {
  .btn--accent:hover:hover {
    background: var(--hover-btn);
    color: var(--belyy);
  }
}

.btn--border {
  border-radius: 4px;
  padding: 12px 48px;
  background: transparent;
  font-family: var(--font-family);
  font-weight: 500;
  color: var(--chernyy);
  transition: background 0.2s;
  border: 1px solid var(--bezhevyy);
  border-radius: 4px;
}
@media (max-width: 767px) {
  .btn--border {
    font-size: calc(16px + 0.6 * (100vw - 320px) / 1440);
  }
}
@media (min-width: 767px) {
  .btn--border {
    font-size: calc(16px + 2 * 100vw / 1440);
  }
}
@media (any-hover: hover) {
  .btn--border:hover:hover {
    background: var(--hover-btn);
    color: var(--belyy);
  }
}

.section {
  margin-bottom: 120px;
}
@media (max-width: 767px) {
