.trouble {

  .inner {
    display:               grid;
    grid-template-columns: 710px 1fr;
    gap:                   clamp(20px, 2.5vw, 40px);
  }

  .image-block {
    position:       relative;
    padding:        40px;
    display:        flex;
    flex-direction: column;

    img {
      position:      absolute;
      inset:         0;
      z-index:       -1;
      border-radius: 8px;
    }

    .title {
      margin: auto 0 10px 0;
    }

    p {
      font-family: var(--second-family);
      font-weight: 700;
      font-size:   20px;
      line-height: 120%;
      color:       var(--belyy);
      margin:      0;
    }
  }

  .items {
    display: grid;
    gap:     10px;

    .item {
      border-radius:         8px;
      padding:               14px 20px;
      background:            var(--seryy);
      display:               grid;
      grid-template-columns: 48px 1fr;
      align-items:           center;
      gap:                   20px;

      .subtitle {
        font-family:   var(--second-family);
        font-weight:   700;
        @include r-font(24, 20);
        line-height:   120%;
        color:         var(--chernyy);
        margin-bottom: 8px;
      }
    }
  }
}