.hero {
  background: url("../../img/hero-bg.webp") center / cover no-repeat;
  margin-bottom: 60px;

  &__wrapper {
    position: relative;
  }

  &__content {
    max-width:      780px;
    padding-top:    58px;
    padding-bottom: 190px;
  }

  &__title {
    margin:         0;
    font-family:    var(--second-family);
    font-weight:    400;
    @include r-font(60, 40);
    line-height:    120%;
    text-transform: uppercase;
    color:          var(--belyy);
    margin-bottom:  20px;

    span {
      color: var(--bezhevyy);
    }
  }

  &__list {
    display:       grid;
    gap:           17px;
    margin-bottom: clamp(40px, 3.5vw, 60px);

    li {
      color:        var(--belyy);
      position:     relative;
      padding-left: 36px;

      &:before {
        content:    '';
        position:   absolute;
        top:        -3px;
        left:       0;
        width:      28px;
        height:     28px;
        background: url('../../img/icons/check-3d.png') no-repeat;
      }
    }
  }

  &__consult {
    background:      rgba(243, 245, 255, 0.8);
    border-radius:   8px;
    padding:         14px 40px;

    display:         flex;
    justify-content: center;
    align-items:     center;
    gap:             14px;
    margin-bottom:   20px;
    max-width:       650px;
  }

  &__phone {
    display:         flex;
    flex-direction:  column;
    gap:             4px;
    align-items:     center;
    justify-content: center;

    // font style
    font-family:     var(--second-family);
    font-weight:     700;
    @include r-font(24, 20);
    line-height:     120%;
    text-align:      center;
    color:           var(--chernyy);
    transition:      color .3s ease-in-out;

    span {
      font-family: var(--font-family);
      font-weight: 400;
      font-size:   14px;
      text-align:  center;
      color:       rgba(35, 33, 31, 0.6);
    }

    @include hover {
      &:hover {
        color: var(--bezhevyy);
      }
    }
  }

  &__buttons {
    display:               grid;
    grid-template-columns: 0.5fr 1fr;
    gap:                   10px;
    max-width:             650px;

    .hero__btn {
      padding: 12px;
    }

    .btn--border {
      backdrop-filter: blur(60px);
      background:      rgba(233, 235, 246, 0.01);
      border:          1px solid #e9ebf6;
      color:           var(--belyy);
    }
  }

  &__image {
    position:  absolute;
    bottom:    0;
    right:     0;
    width:     45vw;
    max-width: 500px;

    img {
      display: block;
    }
  }

  &__futures {
    position: absolute;
    right:    0;
    bottom:   0;
    max-width: 710px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;

    .item {
      border-radius: 4px 4px 0 0;
      padding: 20px 14px;
      height: 260px;
      background: linear-gradient(226deg, #f1b378 0%, #b97637 100%);
      display: flex;
      flex-direction: column;
      gap: 10px;
      color: var(--belyy);
      p {
        margin: 0;
        font-family: var(--second-family);
        font-weight: 700;
        @include r-font(20, 16);
        line-height: 120%;
        color: inherit;
      }

      &:nth-child(even) {
        background: var(--goluboy);
        color: var(--chernyy);
      }
    }
  }
}