// colors
:root {
  // base
  --font-family: "Inter", sans-serif;
  --second-family: "PT Serif", sans-serif;
  --content-width: 1440px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --chernyy: #23211f;
  --siniy: #09165d;
  --bezhevyy: #e6ab73;
  --goluboy: #f3f5ff;
  --belyy: #fff;
  --seryy: #f8f9fc;
  --hover-btn: #B16E2F;
}
