.salon {
  &__title {
    margin-bottom: clamp(40px, 3.5vw, 60px);
    color:         var(--chernyy);
  }

  &__items {
    display:               grid;
    gap:                   20px;
    grid-template-columns: repeat(2, 1fr);
    @media(min-width: 767px) {
      grid-template-columns: repeat(auto-fit, minmax(345px, 1fr));
    }

    .item {
      display:       flex;
      align-items:   center;
      gap:           10px;
      border-radius: 8px;
      padding:       14px;
      background:    var(--goluboy);

      p {
        font-family: var(--second-family);
        font-weight: 700;
        @include r-font(24, 20);
        line-height: 120%;
        color:       var(--chernyy);
      }
    }
  }

  &__link {
    display:         flex;
    justify-content: center;
    align-items:     center;
    gap:             10px;
    max-width:       340px;
    margin:          clamp(20px, 2.5vw, 40px) auto 0 auto;
  }
}