.services {
  &__title {
    margin-bottom: clamp(40px, 3.5vw, 60px);
    color:         var(--chernyy);
    max-width:     745px;
  }

  &__inner {
    display:               grid;
    grid-template-columns: 1fr 275px;
    gap:                   20px;
  }

  .items {
    display:               grid;
    grid-template-columns: repeat(4, 1fr);
    gap:                   20px;

    .item {
      display:        flex;
      flex-direction: column;
      gap:            10px;
      border-radius:  8px;
      padding:        14px;
      background:     var(--goluboy);
      position:       relative;

      .item-icon {
        transition: .3s ease-in-out;
      }

      p {
        font-family: var(--second-family);
        font-weight: 700;
        @include r-font(24, 20);
        line-height: 120%;
        color:       var(--chernyy);
      }

      .item-link {
        position: absolute;
        inset:    0;
        z-index:  1;
      }

      @include hover {
        &:hover {
          .item-icon {
            scale:  1.05;
            rotate: 5deg;
          }
        }
      }
    }
  }

  &__link {
    display:         flex;
    align-items:     center;
    gap:             14px;
    justify-content: center;

    font-family:     var(--second-family);
    font-weight:     700;
    @include r-font(24, 20);
    line-height:     120%;
    color:           var(--belyy);
    border-radius:   8px;
    padding:         20px;
    background:      linear-gradient(226deg, #f1b378 0%, #b97637 100%);
    position:        relative;
    overflow:        hidden;
    transition:      opacity .3s ease-in-out;
    will-change:     opacity;

    @include hover {
      &:hover {
        opacity: .7;
      }
    }

  }
}