.faq {
  .title {
    color:         var(--chernyy);
    margin-bottom: clamp(40px, 3vw, 60px);
  }

  &__items {
    display: grid;
    gap:     14px;
  }

  &__item {
    border:        1px solid transparent;
    border-radius: 4px;
    padding:       14px 20px;
    background:    var(--seryy);
    transition:    border-color ease-in-out .15s;
    cursor:        pointer;

    &-head {
      display:         flex;
      align-items:     center;
      justify-content: space-between;
      pointer-events: none;
    }

    &-title {
      font-family: var(--second-family);
      font-weight: 700;
      @include r-font(24, 20);
      line-height: 120%;
      color:       var(--chernyy);
    }

    &-toggler {
      min-width:     32px;
      height:        32px;
      position:      relative;
      background:    linear-gradient(226deg, #f1b378 0%, #b97637 100%);
      border-radius: 50%;

      &:before {
        content:       '';
        position:      absolute;
        top:           50%;
        left:          50%;
        transform:     translate(-50%, -50%);
        height:        11px;
        width:         2px;
        background:    var(--belyy);
        border-radius: 4px;
        transition: .3s ease;
      }

      &:after {
        content:       '';
        position:      absolute;
        top:           50%;
        left:          50%;
        transform:     translate(-50%, -50%);
        height:        2px;
        width:         11px;
        background:    var(--belyy);
        border-radius: 4px;
        transition: .3s ease;
      }
    }

    &-content {
      display: grid;
      grid-template-rows: 0fr;
      transition: .3s ease-in-out;
      overflow: hidden;

      .content {
        overflow: hidden;
        opacity: 0;
        transition: opacity .3s ease-in-out;
      }
    }
  }
}

.faq__item--active {
  border-color: var(--bezhevyy);
  .faq__item-content {
    grid-template-rows: 1fr;
    margin-top: 20px;

    .content {
      opacity: 1;
    }
  }
  .faq__item-toggler:before {
    rotate: -5deg;
    opacity: 0;
  }
}