.header {
  padding-top: 10px;

  &__wrapper {
    display:     flex;
    align-items: center;
  }

  .logo {
    display:      flex;
    align-items:  center;
    gap:          10px;
    margin-right: 10px;

    span {
      font-family: var(--font-family);
      font-weight: 400;
      font-size:   16px;
      line-height: 130%;
      color:       var(--chernyy);
      max-width:   164px;
    }
  }

  &__year {
    font-family:   var(--second-family);
    font-weight:   700;
    @include r-font(20, 16);
    line-height:   120%;
    color:         var(--chernyy);
    border-radius: 4px;
    padding:       4px 16px;
    background:    var(--goluboy);
  }

  &__actions {
    margin-left: auto;
    display:     flex;
    align-items: center;
    gap:         20px;

    .socials {
      display:     flex;
      align-items: center;
      gap:         10px;
    }
  }

  .header__block {
    display:        flex;
    flex-direction: column;
    gap:            3px;

    span {
      font-family: var(--font-family);
      font-weight: 400;
      font-size:   12px;
      color:       rgba(35, 33, 31, 0.6);
    }
  }

  .header__phone {
    font-family: var(--second-family);
    font-weight: 700;
    font-size:   20px;
    line-height: 120%;
    color:       var(--chernyy);
    transition:  color .3s ease-in-out;
    @include hover {
      &:hover {
        color: var(--bezhevyy);
      }
    }
  }

  &__buttons {
    display:        flex;
    flex-direction: column;
    gap:            8px;
  }

  &__bottom {
    backdrop-filter: blur(14px);
    background:      rgba(35, 33, 31, 1);
    padding-block:   10px;
    margin-top:      10px;
  }

  &__menu {
    .list {
      display:         flex;
      gap:             20px;
      justify-content: space-between;
    }

    .link {
      font-family: var(--font-family);
      font-weight: 500;
      color:       var(--belyy);
      @include r-font(18, 16);
      transition: color .3s ease-in-out;
      @include hover {
        &:hover {
          color: var(--bezhevyy);
        }
      }
    }
  }
}
