.price {
  &__title {
    margin-bottom: 20px;
    color:         var(--chernyy);
  }

  .text-d {
    color:         var(--chernyy);
    max-width:     710px;
    margin-bottom: clamp(40px, 3.5vw, 60px);
  }

  .tabs {
    &__list {
      display:         flex;
      flex-wrap:       wrap;
      justify-content: space-between;
      gap:             20px;
    }

    & .item {
      flex: 1 1 450px;
    }

    &__btn {
      font-family:   var(--second-family);
      font-weight:   700;
      @include r-font(20, 18);
      line-height:   120%;
      text-align:    center;
      color:         var(--chernyy);
      cursor:        pointer;
      border-radius: 8px;
      padding:       20px;
      background:    var(--seryy);
      width:         100%;
      transition:    background-color .3s ease-in-out, color .3s ease-in-out;
      @include hover {
        &:hover {
          background-color: var(--chernyy);
          color:            var(--belyy);
        }
      }

      &._is-active {
        background-color: var(--chernyy);
        color:            var(--belyy);
      }
    }

    &__items {
      margin-top: clamp(20px, 2.5vw, 40px);
    }

    &__item {
      display:   none;
      flex-wrap: nowrap;
      gap:       20px;
      &.active {
        display: flex;
      }
    }
  }

  &__item {
    display:        flex;
    flex-direction: column;
    border-radius:  8px;
    box-shadow:     0 1px 3px 1px rgba(0, 0, 0, 0.15);
    background:     var(--belyy);



    .bottom {
      padding-inline: 20px;
      padding-bottom: 20px;
    }


  }

  &__name {
    font-family:    var(--second-family);
    font-weight:    400;
    font-size:      clamp(18px, 3vw, 32px);
    text-transform: uppercase;
    text-align:     center;
    color:          var(--belyy);
    border-radius:  8px;
    padding:        20px;
    background:     linear-gradient(227deg, #f1b378 0%, #b97637 100%);
    margin-bottom:  40px;
  }

  &__list {
    padding-inline: 20px;
    flex:           1 1 auto;
    display:        flex;
    flex-direction: column;
    gap:            8px;
    margin-bottom:  30px;

    li {
      font-family:  var(--font-family);
      font-weight:  400;
      @include r-font(16, 14);
      line-height:  130%;
      color:        var(--chernyy);
      position:     relative;
      padding-left: 20px;

      &:before {
        content:       '';
        position:      absolute;
        top:           50%;
        left:          0;
        transform:     translateY(-50%);
        width:         8px;
        height:        8px;
        background:    linear-gradient(227deg, #f1b378 0%, #b97637 100%);
        border-radius: 50%;
      }

      &.not-active:before {
        background: #b5b5b5;
      }
    }
  }

  .price__total {
    font-family:    var(--second-family);
    font-weight:    400;
    @include r-font(40, 28);
    text-transform: uppercase;
    text-align:     center;
    color:          var(--chernyy);
    margin-bottom:  14px;
  }

  &__btn {
    display:         flex;
    align-items:     center;
    gap:             10px;
    justify-content: center;
    font-family:     var(--font-family);
    font-weight:     500;
    font-size:       18px;
    color:           var(--chernyy);
    border:          1px solid #dfa960;
    border-radius:   4px;
    padding:         12px 20px;
    width:           100%;
    transition:      background-color .3s ease-in-out, color .3s ease-in-out;
    @include hover {
      &:hover {
        background-color: var(--chernyy);
        color:            var(--belyy);
        border-color:     var(--chernyy);
      }
    }
  }
}